import "./styles.css";
import React from "react";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  ResponsiveContainer
} from "recharts";
import { customColor } from "./CostamizeStyle";
import Legend from "./Legend";

const data = [
  { name: "おこわ", value: 4800 },
  { name: "もち", value: 2300 },
  { name: "おこわ", value: 3300 },
  { name: "みそ", value: 4200 },
  { name: "その他", value: 5000 }
];

const axisFontSize = {
  fontSize: 12
};

export default function Product() {
  const COLORS = customColor;
  return (
    <React.Fragment>
      <Typography component="h2" variant="h6" gutterBottom>
        商品別売上
      </Typography>
      <Grid container alignItems="center" justifyContent="center">
        <Grid item xs={7}>
          <ResponsiveContainer width="80%" aspect="1.2">
            <BarChart
              data={data}
              margin={{ top: 30, right: 0, bottom: 0, left: 0 }}
            >
              <CartesianGrid horizontal={true} vertical={false} />
              <Bar dataKey="value">
                {data.map((entry, index) => (
                  <Cell
                    key={`cell-${index}`}
                    fill={COLORS[index % COLORS.length]}
                  />
                ))}
              </Bar>
              <XAxis dataKey="name" tick={axisFontSize} />
              <YAxis tick={axisFontSize} />
            </BarChart>
          </ResponsiveContainer>
        </Grid>
        <Grid item xs={5}>
          <Legend array={data} />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
