import "./styles.css";
import React from "react";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import { PieChart, Pie, Cell, Label, ResponsiveContainer } from "recharts";
import { customColor } from "./CostamizeStyle";
import Legend from "./Legend";

const data = [
  { name: "愛菜館", value: 1800 },
  { name: "さんフレッシュ", value: 2300 },
  { name: "かわはら夢菜館", value: 3300 },
  { name: "わったいな", value: 4200 }
];

const total = data.reduce(function (sum, element) {
  return sum + element.value;
}, 0);

export default function StoreSales() {
  const COLORS = customColor;
  const numberWithComma = new Intl.NumberFormat();
  return (
    <React.Fragment>
      <Typography component="h2" variant="h6" gutterBottom>
        今日の売上
      </Typography>
      <Grid container alignItems="center" justifyContent="center">
        <Grid item xs={6}>
          <ResponsiveContainer width="80%" aspect="1">
            <PieChart>
              <Pie
                data={data}
                innerRadius="55%"
                outerRadius="80%"
                fill="#8884d8"
                paddingAngle={1}
                dataKey="value"
              >
                <Label
                  value={"¥" + numberWithComma.format(total)}
                  position="center"
                  style={{ fontSize: "160%" }}
                />
                {data.map((entry, index) => (
                  <Cell
                    key={`cell-${index}`}
                    fill={COLORS[index % COLORS.length]}
                  />
                ))}
              </Pie>
            </PieChart>
          </ResponsiveContainer>
        </Grid>
        <Grid item xs={6}>
          <Legend array={data} />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
