import "./styles.css";
import * as React from "react";
import Checkbox from "@material-ui/core/Checkbox";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import StopIcon from "@material-ui/icons/Stop";
import { customColor } from "./CostamizeStyle";

export default function Legend(props) {
  const COLORS = customColor;
  const numberWithComma = new Intl.NumberFormat();
  return (
    <Table size="small">
      <TableBody>
        {props.array.map((items, index) => (
          <TableRow key={index} role="checkbox">
            <TableCell padding="checkbox">
              <Checkbox
                disabled
                size="small"
                icon={
                  <StopIcon style={{ color: COLORS[index % COLORS.length] }} />
                }
              />
            </TableCell>
            <TableCell component="th" scope="row" padding="none">
              {items.name}
            </TableCell>
            <TableCell align="right">
              ¥{numberWithComma.format(items.value)}
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
}
